    .banner-slider{
        max-width: 1440px;
        margin: 0 auto;
        width: 100%;
        margin-top: 1rem;
        .slick-dots{
            bottom: 20px;
        }
        .slick-prev{
            left: 15px;
        }
        .slick-next{
            right: 15px;
        }
        .slick-prev, .slick-next {
         z-index: 1; /* 다른 요소 위에 보이도록 */
        width: 40px;
        height: 40px;
        }       
        .slick-prev:before, .slick-next:before {
        font-size: 3rem; /* 화살표 아이콘 크기 */
        color: #fff; /* 화살표 색상 */
        }
    img{
        width: 100%;
        @include tablet{
            width: 100%;
            height: 25rem;
            margin: 0;
        }
        @include desktop{
            object-fit: cover;
            object-position: center;
            width: 100%;
            height: 40rem;
            margin: 0;
        }
    }
    }
